.header {
  position: relative;
  z-index: 2;
  padding: 20px 0;
  font-family: FiraSans;
}
.header_row {
  display: flex;
  justify-content: space-between;
}
.header_right {
  display: flex;
  align-items: center;
}
.header_center {
  margin-top: 14px;
}
.header_search {
  width: 28px;
  height: 16px;
  background-image: url("../img/search.svg");
  background-position: top left;
  background-repeat: no-repeat;
  margin-right: 24px;
  align-items: center;
}
.header_lang-ico {
  border-radius: 4.645px;
  border: 1.5px solid #FFF;
  width: 56px;
  color: #FFF;
  font-size: 12.386px;
  font-weight: 700;
  line-height: 28.642px;
  letter-spacing: -0.619px;
  padding-left: 13px;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.header_lang-ico:hover {
  background: rgba(240, 238, 238, 0.16);
}
.header_lang-ico:after {
  content: '';
  width: 16px;
  height: 6px;
  background-image: url("../img/arrow-1.svg");
  background-position: top left;
  background-repeat: no-repeat;
  margin-left: 3px;
  margin-top: 2px;
}
.header_nav {
  flex-grow: 1;
  display: none;
}
.header_nav:not(:first-child) {
  margin-top: 24px;
}
.header_nav li {
  margin: 0 15px;
}
.header_nav li a {
  color: #FFF;
  font-size: 15px;
  line-height: 111%;
  text-decoration: none;
}
.header_nav li a:hover {
  text-decoration: underline;
}
.clone-header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;
  transition: 0.3s;
}
.translate {
  transform: translateY(-100%);
}
.translate.active {
  transform: translateY(0);
}
.clone-header .header {
  background: #025EA1;
  padding: 16px 0;
}
@media (max-width: 767px) {
  .clone-header .header {
    padding: 5px 0;
  }
}
.clone-header .header_nav {
  display: flex;
}
@media (max-width: 1220px) {
  .clone-header .header_nav {
    display: none;
  }
}
@media (max-width: 767px) {
  .clone-header .header_logo img {
    width: 115px;
  }
}