.news {
  padding: 50px 0;
  &__top {
    display: inline-flex;
    align-items: center;
  }
  &__select:not(:first-child) {
    margin-left: 24px;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 72px / 4));
    gap: 24px;
    margin-top: 32px;
  }
  .section-5_col {
    width: 100%;
    padding: 0;
  }
  &__btn {
    width: 266px;
    height: 65px;
    margin-top: 40px;
    span {
      font-size: 21px;
      line-height: 120%;
      font-weight: 700;
    }
  }
}

@media screen and (max-width: 1200px) {
  .news {
    &__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 48px / 3));
    }
  }
}

@media screen and (max-width: 767px) {
  .news {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 16px / 2));
      gap: 16px;
    }
    .section-5_col {
      margin: 0;
    }
    &__btn {
      width: 200px;
      height: 50px;
      span {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .news {
    &__top {
      flex-direction: column;
    }
    &__select {
      &:not(:first-child) {
        margin: 16px 0 0 0;
      }
    }
  }
}