.nav {
  padding: 50px 0;
  li {
    font-size: 20px;
    line-height: 150%;
    &:not(:first-child) {
      margin-top: 15px;
    }
    a {
      color: #000000;
      &:hover {
        opacity: 0.6;
      }
    }
  }
}
.text16 {
  font-size: 16px;
  line-height: 140%;
}
.text21 {
  font-size: 21px;
  line-height: 140%;
}
.text35 {
  font-family: FiraSans;
  font-size: 35px;
  line-height: 140%;
  font-weight: 700;
  text-transform: uppercase;
  position: relative;
  padding-bottom: 19px;
  &:after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 80px;
    height: 10px;
    background: #025EA1;;
    border-radius: 7px;
  }
}
.text40 {
  font-size: 40px;
  line-height: 140%;
  text-transform: uppercase;
}
.page-top {
  font-family: FiraSans;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  color: #ffffff;
  &__inner {
    min-height: 225px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  &__desc {
    padding: 45px 0;
  }
  &__title {
    font-weight: 400;
  }
  &__text {
    max-width: 832px;
    margin-top: 16px;
    p {
      &:not(:first-child) {
        margin-top: 16px;
      }
    }
  }
}
.tabs {
  padding: 50px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__left {
    width: calc(100% - 570px);
  }
  &__right {
    width: 510px;
    margin-left: 60px;
  }
  &__btns {
    display: inline-flex;
    align-items: center;
    background: #F7F8FA;
    border-radius: 55px;
  }
  &__btn {
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #F7F8FA;
    border-radius: 55px;
    padding: 5px 48px;
    font-family: FiraSans;
    font-weight: 700;
    text-transform: uppercase;
    cursor: pointer;
    &--active {
      background: #025EA1;
      color: #ffffff;
    }
  }
  &__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__desc {
    min-height: 300px;
    flex-direction: column;
   justify-content: center;
    background: #F0F7FC;
    border-radius: 12px;
    padding: 38px 45px;
    margin-top: 32px;
    display: none;
    &--active {
      display: flex;
    }
  }
  &__list {
    margin-top: 16px;
    li {
      list-style: disc inside;
      padding-left: 8px;
      &:not(:first-child) {
        margin-top: 16px;
      }
    }
  }
  &__img {
    img {
      width: 100%;
    }
  }
}
.swiper-button-prev {
  width: 24px;
  svg {
    width: 100%;
    height: auto;
  }
  &:after {
    display: none;
  }
}
.swiper-button-next {
  width: 24px;
  svg {
    width: 100%;
    height: auto;
  }
  &:after {
    display: none;
  }
}
.btn-blue {
  background: #025EA1;
  border-radius: 10px;
  span {
    color: #ffffff;
    text-transform: uppercase;
  }
  &:hover {
    background: #004f86;
  }
}
.up {
  position: fixed;
  z-index: 10;
  right: 20px;
  bottom: 20px;
  z-index: 2;
  width: 37px;
  height: 37px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  border: 1px solid #333333;
  cursor: pointer;
  opacity: 0;
  pointer-events: none;
  transition: all 0.5s;
  &--active {
    opacity: 1;
    pointer-events: all;
  }
}
.nice-select {
  width: 215px;
  height: 40px;
  background: #ffffff;
  border: 1px solid #7E7E7E;
  border-radius: 8px;
  padding: 3px 54px 3px 16px;
  font-size: unset;
  line-height: unset;
  display: flex;
  align-items: center;
  position: relative;
  &:after {
    top: 50%;
    right: 10px;
    transform: translate(0, -50%);
    width: 16px;
    height: 10px;
    background-image: url("../img/arrow-select.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 16px 10px;
  }
  &:before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    width: 38px;
    height: 100%;
    background: #F7F8FA;
    border-radius: 0 8px 8px 0;
    pointer-events: none;
  }
  &:hover {
    border: 1px solid #7E7E7E;
  }
  .current {
    font-family: FiraSans;
    font-size: 16px;
    line-height: 120%;
    font-weight: 400;
  }
  .list {
    li {
      font-family: FiraSans;
      font-size: 14px;
      line-height: 120%;
      font-weight: 400;
      padding: 10px 16px;
    }
  }
}
.nice-select.open:after {
  transform: translate(0, -50%) rotate(0);
}

@media screen and (max-width: 1200px) {
  .tabs {
    &__left {
      width: calc(100% - 430px);
    }
    &__right {
      width: 400px;
      margin-left: 30px;
    }
    &__desc {
      padding: 30px 20px;
    }
  }
}

@media screen and (max-width: 992px) {
  .tabs {
    &__inner {
      display: block;
    }
    &__left {
      width: 100%;
    }
    &__right {
      max-width: 400px;
      width: 100%;
      margin: 24px auto 0;
    }
  }
}

@media screen and (max-width: 767px) {
  .text16 {
    font-size: 14px;
  }
  .text21 {
    font-size: 16px;
  }
  .text35 {
    font-size: 20px;
    padding-bottom: 8px;
    &:after {
      width: 32px;
      height: 3.8px;
    }
  }
  .text40 {
    font-size: 24px;
  }
  .tabs {
    padding: 30px 0 15px;
    &__right {
      max-width: 290px;
    }
    &__btn {
      height: 40px;
      font-weight: 400;
      padding: 3px 16px;
    }
    &__desc {
      min-height: 100%;
      padding: 16px;
    }
  }
  .swiper-button-prev {
    width: 14px;
  }
  .swiper-button-next {
    width: 14px;
  }
  .nice-select {
    .current {
      font-size: 14px;
    }
  }
}

@media screen and (max-width: 400px) {
  .text21 {
    font-size: 14px;
  }
  .page-top {
    &__inner {
      min-height: 112px;
      padding: 25px 0;
    }
    &__text {
      margin-top: 5px;
    }
  }
}